import {useState, useEffect} from 'react';
import {Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, NavBtn, NavBtnModal} from './NavbarElements';
import {List } from 'react-bootstrap-icons';
import {Jumbotron} from 'reactstrap';
import { animateScroll as scroll } from 'react-scroll';
import ContactMe from './ContactMe';

const  Navbar = ({ toggle }) => {

    const [scrollNav, setScrollNav] = useState(false);

    const changeNav = () => {
        if(window.scrollY >= 80){
            setScrollNav(true);
        }else{
            setScrollNav(false);
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return ( 
        <>
            <Nav id="nav" scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo id="navLogo" to="home" onClick={toggleHome}>portfolio</NavLogo>
                    <MobileIcon onClick={toggle}>
                        <List />
                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks to="portfolio"
                            smooth={true} 
                            duration={700} 
                            spy={true} 
                            exact='true' 
                            offset={-80}
                            >Portfolio</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="education"
                            smooth={true} 
                            duration={700} 
                            spy={true} 
                            exact='true' 
                            offset={-80}>Education</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="skillset"
                            smooth={true} 
                            duration={700} 
                            spy={true} 
                            exact='true' 
                            offset={-200}>Skill Set</NavLinks>
                        </NavItem>
                    </NavMenu>
                    <NavBtn>
                        <ContactMe />
                    </NavBtn>
                </NavbarContainer>
            </Nav>
            <Jumbotron className="mb-0">
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1 id="uno">Welcome to my portfolio.</h1> 
                                <p id="dos" style={{color: '#98fa94'}}>Here you will find part of my work as Front-End developer.</p>

                            </div>
                        </div>
                    </div>
            </Jumbotron>
        </>
    );
}
 
export default Navbar ;